import { sendList } from "../chrome/utils";
import { getVideoObject } from "../utils";
import { VideoType } from "../types/MediaTypes";

const currentLocation = window.location;

const getM3u8 = function () {
  const videoId = currentLocation.pathname;
  fetch(`https://www.dailymotion.com/player/metadata${videoId}`)
    .then((response) => response.json())
    .then((data) => getM3u8File(data.qualities.auto[0].url));
};

const getM3u8File = function (url: string) {
  var xhr = new XMLHttpRequest();
  xhr.open("GET", url, false);
  xhr.send();
  parsM3u8File(xhr.responseText);
};

const parsM3u8File = function (text: string) {
  const rows = text.split("\n");

  const itemRows = rows.map((i) => {
    return i.split(",");
  });

  const newArr: string[][] = [];
  itemRows.forEach((i) => {
    if (i.length < 6) {
      return;
    } else {
      newArr.push(i);
    }
  });
  const result = newArr.filter((e, i) => !(i % 2));
  const urls = result.map((i) => {
    if (i[5]) {
      // @ts-ignore
      const res = /PROGRESSIVE-URI="(.*)"/?.exec(i)
      return res ? res[1] : '';
    }
    else {
      return '';
    }
  }).filter(item => item !== '');
  creatList(urls);
};

const creatList = function (arr: string[]) {
  const a = document.title;
  const b = a.split(" ");
  b.splice(-3, 3);
  const title = b.join(" ");
  const list: VideoType[] = [];
  const promisesList: Promise<void>[] = [];

  arr.forEach((i) => {
    const promise = getVideoObject(title, i).then((obj) => {
      list.push(obj);
    });
    promisesList.push(promise)
  });
  Promise.all(promisesList).then(() => {
    sendList({
      list,
      header: title,
      id: (new Date()).toString(),
    });
  })
};

getM3u8();

chrome.runtime.onMessage.addListener(message => {
  if (message.action === "pageUpdated") {
    getM3u8();
  }
});